@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial {
    background-color: v.$secondary;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 1rem;
    color: v.$white;

    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet { 
        margin-bottom: 0;

        &:nth-child(1) {
            grid-column: 1 / 4;
        }

        &:nth-child(3) {
            grid-column: 5 / 6;
            grid-row: 1 / 3;
        }

        &:nth-child(4) {
            grid-column: 1 / 3;
        }

        &:nth-child(5) {
            grid-column: 3 / 5;
        }

     }

    &Header {
        display: flex;
        gap: 2rem;
        align-items: center;
    }

    &Image {
        flex-basis: 10rem;
        border-radius: 50%;
        overflow: hidden;
    }

    &Author {
        color: v.$white;
        font-weight: bold;
        margin: .5rem;
    }

    &Text {
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0 0 0;
        padding-left: 3.5rem;
        position: relative;

        &::before {
            content: ' " ';
            font-size: 10rem;
            position: absolute;
            left: 0;
            top: -4rem;
            opacity: .2;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }
    }

}