.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.download {
  background-color: #eceeef;
  padding: 5rem 0;
}
.downloadGrid {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .downloadGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.downloadImage {
  width: 50%;
  margin: 5rem auto 0 auto;
  transition-property: transform;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .downloadImage {
    margin: 0 auto;
  }
}
.downloadImage:hover {
  transform: rotate(4deg) scale(1.2);
}

.stores {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.storesHeading {
  text-align: center;
  color: #455a64;
  margin: 2rem 0 0 0;
}
.storesText {
  text-align: center;
  margin-bottom: 3rem;
}
.storesGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
.storesLink {
  transition-property: transform;
  transition-duration: 500ms;
}
.storesLink:hover {
  transform: scale(1.2);
}

.favorite {
  background-color: #FFFFFF;
  padding: 4rem;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 2.6rem -0.8rem #99a4a9;
  border-radius: 1rem;
}
.favorite:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorite {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .favoriteGrid {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .favoriteGrid {
    grid-template-columns: 1fr 3fr;
    gap: 4rem;
  }
}
.favoriteImage {
  max-width: 20rem;
  margin: 0 auto 2rem auto;
}
.favoriteContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favoriteContent {
    align-items: flex-start;
  }
}
.favoriteName {
  margin: 1rem 0;
  color: #e8505b;
  font-size: 2.4rem;
}
.favoriteDescription {
  margin: 0.5rem 0;
  text-align: center;
  font-size: 1.6rem;
  color: #455a64;
  line-height: 1.7;
}
@media (min-width: 768px) {
  .favoriteDescription {
    text-align: left;
  }
}

.favorites {
  background-color: #eceeef;
  padding: 5rem 0;
}
.favoritesHeading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favoritesGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footerGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
.footerWidget {
  margin-bottom: 5rem;
}
.footerWidget:last-of-type {
  margin-bottom: 0;
}
.footerHeading {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footerHeading {
    text-align: left;
  }
}
.footerNav {
  display: flex;
  flex-direction: column;
}
.footerNav, .footerText {
  margin-top: 2rem;
}
.footerLink, .footerText {
  font-size: 2rem;
  line-height: 1.8;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .footerLink, .footerText {
    text-align: left;
  }
}
.footerCopyright {
  background-color: #e8505b;
  margin: 0;
  color: #FFFFFF;
  text-align: center;
  padding: 2rem;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.headerLogo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .headerLogo {
    margin-bottom: 0;
  }
}

.logoApp {
  width: 40rem;
}

.navegation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .navegation {
    width: auto;
    flex-direction: row;
  }
}
.navegationLink {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegationLink--Registrar {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navegationLink--Registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navegationLink--Registrar {
    width: auto;
  }
}

@media (min-width: 768px) {
  .formGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.formContent {
  background-color: #e8505b;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.formHeading {
  color: #FFFFFF;
  text-align: center;
}
.formField {
  margin-bottom: 1rem;
}
.formLabel {
  display: block;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 1rem 0;
}
.formInput {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.formSubmit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formSubmit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .formSubmit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formSubmit {
    width: 100%;
  }
}
.formImage-Container {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.formImage {
  width: 30rem;
}
@media (min-width: 1024px) {
  .formImage {
    width: 100%;
  }
}

.steps {
  margin-top: 5rem;
}
.stepsWave {
  margin-bottom: -1rem;
}
.stepsContent {
  background-color: #e8505b;
}
.stepsHeading {
  text-align: center;
  color: #FFFFFF;
}
.stepsHeading--Description {
  font-size: 1.8rem;
  display: block;
}
@media (min-width: 768px) {
  .stepsGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.stepsImage {
  transition-property: transform;
  transition-duration: 300ms;
}
.stepsImage:hover {
  transform: rotate(-4deg) scale(1.2);
}

.dealers {
  padding: 5rem 0;
}
.dealersHeading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .dealersGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.dealersContent {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.dealersText {
  line-height: 1.5;
  color: #455a64;
}
.dealersMoreInfo {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.dealersMoreInfo:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .dealersMoreInfo {
    width: auto;
  }
}

.testimonials {
  padding: 5rem 0;
}
.testimonialsHeading {
  text-align: center;
}
@media (min-width: 768px) {
  .testimonialsGrid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}
.testimonialHeader {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonialImage {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonialAuthor {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0.5rem;
}
.testimonialText {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 3.5rem;
  position: relative;
}
.testimonialText::before {
  content: ' " ';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}