@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secondary;
    padding-top: v.$separation;

    &Grid {

        @include m.tablet { 
            @include m.grid(3, 1rem);
         }
    }

    &Widget {
        margin-bottom: v.$separation;

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    &Heading {
        color: v.$white;
        margin: 0;
        text-align: center;

        @include m.tablet { 
            text-align: left;
         }
    }

    &Nav {
        display: flex;
        flex-direction: column;

    }

    &Nav,
    &Text {
        margin-top: 2rem;
    }

    &Link,
    &Text {
        font-size: 2rem;
        line-height: 1.8;
        color: v.$white;
        text-align: center;

        @include m.tablet { 
            text-align: left;
         }
    }

    &Copyright {
        background-color: v.$primary;
        margin: 0;
        color: v.$white;
        text-align: center;
        padding: 2rem;
    }
}